import React, { Component } from 'react';
import anime from 'animejs';
import './App.css';

import Slider from 'react-slick';
import '../node_modules/slick-carousel/slick/slick.css'
import '../node_modules/slick-carousel/slick/slick-theme.css'

import Swiper from 'react-id-swiper';
import '../node_modules/react-id-swiper/src/styles/css/swiper.css'

class App extends Component {

  componentDidMount(){
    console.log('didMount');
    
    anime({
      targets: '.txt1',
      backgroundColor: '#ccc',
      translateX:100,
      opacity:.5,
      duration: 2000,
      loop: true
    });

    anime({
      targets: '.txt2',
      scale:1.5,
      rotate:'1turn',
      direction:'reverse'
    });
    
    anime({
      targets: '.txt3',
      translateX: '50',
      delay: function(target, index){
        return index * 150;
      },
      duration:2000,
      loop: true,
      direction:'alternate'
    });
    

  }

  render() {

    return (
      <div className="App">

          <h2>Slick slider</h2>
          <SimpleSlider />

          <h2>swiper slider</h2>
          <SwiperSlider />

          <h2>animeJS</h2>
          <div className="item">
            <span className="txt1">anime.js</span>
          </div>

          <div className="item">
            <span className="txt2">anime.js</span>
          </div>

          <div className="item">
            <div><span className="txt3">anime.js</span></div>
            <div><span className="txt3">anime.js</span></div>
            <div><span className="txt3">anime.js</span></div>
          </div>




      </div>
    );
  }
}

class SimpleSlider extends React.Component {
  render(){

    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1.2,
      centerMode: true,
      centerPadding: '60px',
      
    };

    return (
      <div>
        <Slider {...settings}>
          <div>
            <h3>1</h3>
          </div>
          <div>
            <h3>2</h3>
          </div>
          <div>
            <h3>3</h3>
          </div>
          <div>
            <h3>4</h3>
          </div>
          <div>
            <h3>5</h3>
          </div>
          <div>
            <h3>6</h3>
          </div>
        </Slider>
      </div>
    )
  }
}


class SwiperSlider extends React.Component {
  render(){

    const params = {
      pagination: {
        el: '.swiper-pagination',
        type: 'bullets',
        clickable: true
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      },
      spaceBetween: 20,
      slidesPerView:1.3,
      centeredSlides:true,
      loop:true
    }

    return (
      <div>
        <Swiper {...params}>
          <div>Slide 1</div>
          <div>Slide 2</div>
          <div>Slide 3</div>
          <div>Slide 4</div>
          <div>Slide 5</div>
        </Swiper>
      </div>
    )
  }
}

export default App;



